import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import 'rxjs/add/operator/map';
import { environment } from '../../environments/environment';
import { ClientCompanyUsers } from '../../pages/_models/client-company-users';
import { Feedback } from '../../pages/_models/feedback';

@Injectable()
export class FeedbackDataServiceProvider {

  public static readonly GET_ALL_COUNTRYCODES = environment.apiUrl + '/countries/all';
  public static readonly SAVE_CLIENT_COMPANY_USER = environment.apiUrl + '/clientCompanyUsers/saveOrUpdate';
  public static readonly GET_USER_BY_PHONE = environment.apiUrl + '/clientCompanyUsers/getListByPhone';
  public static readonly GET_ALL_FEEDBACKS_BY_CUID = environment.apiUrl + '/feedback/getListByData';
  public static readonly GET_ALL_FEEDBACKS_MASTER = environment.apiUrl + '/feedbackMaster/all';

  public static readonly SAVE_FEEDBACK_FOR_USER = environment.apiUrl + '/feedback/saveOrUpdate';
  
  constructor(public http: HttpClient) {
    // console.log('Hello FeedbackDataServiceProvider Provider');
  }

  checkUserSessionService() {
    const currentUser = localStorage.getItem('currentUser');
    if (currentUser != null) {
      const currentUserJson = JSON.parse(currentUser);
      if (currentUserJson.phone !== '') {
        return true;
      } else {
        localStorage.removeItem('currentUser');
        return false;
      }
    } else {
      return false;
    }
  }

  logoutService() {
    localStorage.removeItem('currentUser');
  }

  getAllCountryCodes() {
    const urlToCall = FeedbackDataServiceProvider.GET_ALL_COUNTRYCODES;

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
  }

  saveClientCompanyUser(clientCompanyUsers: ClientCompanyUsers) {
    return this.http.post(FeedbackDataServiceProvider.SAVE_CLIENT_COMPANY_USER + '/', clientCompanyUsers);
  }

  getUserByPhone(phoneNumber) {
    const urlToCall = FeedbackDataServiceProvider.GET_USER_BY_PHONE + '/' + phoneNumber;

    return this.http.get(urlToCall)
    .map(res => <any>res);
  }

  getAllFeedbackByCuid(cuid) {
    const urlToCall = FeedbackDataServiceProvider.GET_ALL_FEEDBACKS_BY_CUID + '/' + cuid;

    return this.http.get(urlToCall)
    .map(res => <any>res);
  }

  saveFeedbackForUser(userFeedbackForm: Feedback) {
    const urlToCall = FeedbackDataServiceProvider.SAVE_FEEDBACK_FOR_USER;
    return this.http.post(urlToCall + '/', userFeedbackForm);
  }

  getAllFeedbackMasterList() {
    const urlToCall = FeedbackDataServiceProvider.GET_ALL_FEEDBACKS_MASTER;
    return this.http.get(urlToCall)
    .map(res => <any>res);
  }
  

}



