import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { FeedbackDataServiceProvider } from '../../providers/feedback-data-service/feedback-data-service';
import { ClientCompanyUsers } from '../_models/client-company-users';
import { FeedbackUiform } from '../_models/feedback-uiform';
import { Feedback } from '../_models/feedback';
import { FeedbackMaster } from '../_models/feedback-master';
import { ClientCompanies } from '../_models/client-companies';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public idCountries: any;
  public countryCodesList: any[] = [];
  public clientCompanyUser: ClientCompanyUsers = new ClientCompanyUsers();
  clientCompanyUserSession: any;
  public sessionAvailable: boolean;
  public isActiveUserStatus: number;

  public showFeedbackSavedSuccess: boolean = false;

  // public feedbackUiformList: FeedbackUiform[] = [];
  public feedbackForm: Feedback = new Feedback();

  feedbackMasterList : FeedbackMaster[];

  constructor(public navCtrl: NavController,
    public feedbackDataServiceProvider: FeedbackDataServiceProvider) {
      this.verifyAndShow();
  }

  verifyAndShow() {
    this.sessionAvailable = this.checkUserSession();
    
          // console.log(this.sessionAvailable + ' -----this.sessionAvailable');
          if(this.sessionAvailable) {
            let clientComUserSession = this.getUserSession();
            let phonesessionTemp = clientComUserSession.phone;
            // console.log('-- phonesessionTemp --- ' + phonesessionTemp);
    
            this.feedbackDataServiceProvider.getUserByPhone(phonesessionTemp).subscribe(data => {
              // console.log(data);
              let ccUserByPhoneObj = data as ClientCompanyUsers;
              // console.log('is activeeeee -- ' + ccUserByPhoneObj.isActive);
    
              this.isActiveUserStatus = ccUserByPhoneObj.isActive;
              // console.log('is activeeeee -- ' + this.isActiveUserStatus);
    
              if(this.isActiveUserStatus === 1) {
                // console.log('is activeeeee -- ');
    
                let cuid = clientComUserSession.idUsers;
                // console.log('is cuid -- ' + cuid);            
    
                this.feedbackDataServiceProvider.getAllFeedbackMasterList().subscribe(data => {
                  // console.log( "feedbackMasterList -- "  + data);
                  this.feedbackMasterList = data as FeedbackMaster[];
                });
    
                // this.feedbackDataServiceProvider.getAllFeedbackByCuid(cuid).subscribe(data => {
                //   console.log(data);
                //   this.feedbackUiformList = data;
                // });
    
              }
    
            });
            
          } else {
            this.feedbackDataServiceProvider.getAllCountryCodes().subscribe(data => {
              // console.log(data);
              this.countryCodesList = data;
            });
          }
  }

  saveClientCompanyFormFn() {
    // console.log('on saveClientCompanyFormFn ');
    // this.feedbackDataServiceProvider.saveClientCompanyUser(this.clientCompanyUser).subscribe(data => {
    //   console.log(data);
    // });
  }

  onSumbitClientCompanyUserForm() {

    // this.clientCompanyUser.country.idCountries = this.idCountries;
    // console.log('on submit ');
    // console.log('on submit ' + this.clientCompanyUser + JSON.stringify(this.clientCompanyUser) );

    if(this.clientCompanyUser.username !== ''
        && this.clientCompanyUser.country !== null
        && this.clientCompanyUser.phone.length === 10
        && this.clientCompanyUser.email !== ''
        && this.clientCompanyUser.clientCompanyName !== '') {

        // console.log('submittingggggggg');

        this.feedbackDataServiceProvider.saveClientCompanyUser(this.clientCompanyUser).subscribe(data => {
          // console.log(data);
          let clientCompanyUserSessionTemp = data as ClientCompanyUsers;
          localStorage.setItem('currentUser', JSON.stringify(clientCompanyUserSessionTemp));
          localStorage.setItem('phoneUser', JSON.stringify(clientCompanyUserSessionTemp.phone));
          localStorage.setItem('emailUser', JSON.stringify(clientCompanyUserSessionTemp.email));

          this.clientCompanyUser = new ClientCompanyUsers();

          this.verifyAndShow();
        });

    }
    else {
      alert('Please provide valid data.');
    }
    
  }

  onChangeCountry(selectedValue){
    console.info("Selected:" + selectedValue);
  }

  checkUserSession() {
    const currentUser = localStorage.getItem('currentUser');
    if (currentUser != null) {
      const currentUserJson = JSON.parse(currentUser);
      if (currentUserJson.phone !== '') {
        return true;
      } else {
        localStorage.removeItem('currentUser');
        return false;
      }
    } else {
      return false;
    }
  }

  getUserSession() {
    const currentUser = localStorage.getItem('currentUser');
    if (currentUser != null) {
      const currentUserJson = JSON.parse(currentUser);
      if (currentUserJson.phone !== '') {
        let clientCompanyUserSessionTemp = currentUserJson as ClientCompanyUsers;
        return clientCompanyUserSessionTemp;
      } 
    } 
    else {

    }
  }

  logout() {
    localStorage.removeItem('currentUser');
  }  


  onSumbitFeedbackForm() {
    this.showFeedbackSavedSuccess = false;
    // console.log('on submit ');
    // console.log('on submit ' + this.feedbackForm + JSON.stringify(this.feedbackForm) );

    // console.log('notes-- ' + this.feedbackForm.notes);
    if(this.feedbackForm.notes
      && this.feedbackForm.feedbackMaster) {

        let clientComUserSession = this.getUserSession();
        let cuid = clientComUserSession.idUsers;

        this.feedbackForm.clientCompanyUser = clientComUserSession;

        this.feedbackDataServiceProvider.saveFeedbackForUser(this.feedbackForm).subscribe(data => {
          this.showFeedbackSavedSuccess = true;
          // console.log(data);
          let feedbackTemp = data as Feedback;
          // console.log(feedbackTemp + ' ----------' );

          this.feedbackForm = new Feedback();
          this.feedbackForm.notes = '';
          this.feedbackForm.feedbackMaster = null;

          // console.log('is cuid -- ' + cuid);            
          // this.feedbackDataServiceProvider.getAllFeedbackByCuid(cuid).subscribe(data => {
          //   console.log(data);
          //   this.feedbackUiformList = data;
          // });

        });

    }
    else {
      alert('Please input topic and details.');
    }
    
  }

  getListFeedbackMasterList() {
    this.feedbackDataServiceProvider.getAllFeedbackMasterList().subscribe(data => {
      // console.log( "feedbackMasterList -- "  + data);
      this.feedbackMasterList = data as FeedbackMaster[];
    });
  }

}



