import { FeedbackMaster } from './feedback-master';
import { ClientCompanyUsers } from './client-company-users';

export class Feedback {
    
    clientFeedbackId: number;
    notes: string;
    feedBackStatus = 'In Progress';
    status :number;
    statusNotes: string;
    usertoken: string;
    feedbackMaster: FeedbackMaster;
    clientCompanyUser: ClientCompanyUsers;

    constructor(
    ) {

    }

    setFeedbackMaster(feedbackMaster: FeedbackMaster): void {
        this.feedbackMaster = feedbackMaster;
    }
    setClientCompanyUser(clientCompanyUser: ClientCompanyUsers) {
        this.clientCompanyUser = clientCompanyUser;
        console.log('clientUser is set');
    }
}



