import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpClientModule } from '@angular/common/http';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { FeedbackPage } from '../pages/feedback/feedback';
import { UserStatusPage } from '../pages/user-status/user-status';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { FeedbackServiceProvider } from '../providers/feedback-service/feedback-service';
import { FeedbackDataServiceProvider } from '../providers/feedback-data-service/feedback-data-service';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    FeedbackPage,
    UserStatusPage
    
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    FeedbackPage,
    UserStatusPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    FeedbackServiceProvider,
    FeedbackDataServiceProvider
  ]
})
export class AppModule {}



